import React from "react";
import Title from "../components/Title";

function Contact() {
  return (
    <section id="contact-section">
      <Title title="Contact" />
      <p className="long-text">
      Pour me contacter, vous pouvez me contacter sur les mdias sociaux ou sur mon courriel en cliquant sur le bouton suivant :
        <div className="button-container">
          <a href="mailto:liviolight@hotmail.com">
            <button className="button">Me contacter</button>
          </a>
        </div>
      </p>
    </section>
  );
}

export default Contact;
